/**/

.admin{
    a{
        display: block;
        color: $dark;
    }
    /*.nav{
        a:hover{
        color: #ffff;
        }
    }*/
    .page-link{
      color: $success;
      &:hover{
        color: #ffff;
        background-color: $success;
      }
    }
    .active-nav-span{
      color: $success;
      &:hover{  /* .text-success class  color  !important */
        color: #ffff ;
        background-color: $success;
      }
    }

    .new-form-a{
        border-bottom: 1px solid $color-1;
    }
    .new-form-b{
      border-top: 1px solid $color-1;
    }
    .new-btn-Cancel{
      padding: 8px 32px;
    }
    .tbody th, tbody td{ /*trthtd*/
      padding-top: 19px;
      padding-bottom: 17px;
    }
    

    /**/
    .modal-content{
        width: 480px;
        padding: 32px;
    }
    .img-modal{
      img{
        width: 141px;
        height: 141px;
      }
    }
    .box-modal{
        width: 251px;
    }
    .collapse-btn{
      padding: 8px 32px;
      margin-top: 32px;
    }
    /**/
    /*
     JS 
     bootstrap  collapse 
     .collapsed  class  css 
    */
    .icon-expand_more {
      display: none;
    }
    .icon-expand_less {
      display: inline-block;
    }
    .collapsed .icon-expand_more {
      display: inline-block;
    }
    .collapsed .icon-expand_less {
      display: none;
    }
}
